/* eslint-disable no-unused-vars */
import React, { useState, useEffect } from 'react';
import './Search.css';
import { useHistory } from 'react-router-dom';
import { axios } from './axios.js';
import { get } from 'axios';
import { IconContext } from 'react-icons';
import BildLogo from './media/iconStart.svg';
import { RiTodoLine } from 'react-icons/ri';
import IconTasc from './media/iconTasc.png';
function Signup(props) {
	const [ShowSomething, setShowSomething] = useState(false);
	useEffect(() => {
		axios.get('/am-i-loged-in').then((res) => {
			if (res.data) {
				console.log('Du bist eingeloggt');

				history.push({
					pathname: '/stundenplan',
					state: {
						login: true,
						message: 'Hallo, du bist eingeloggt!',
					},
				});
				setShowSomething(false);
			} else {
				console.log('Du bist NICHT eingeloggt');
				setShowSomething(true);
			}
		});
	}, []);

	useEffect(() => {
		props.check();
		props.hide(false);
	}, [props]);
	const [LogInData, setLogInData] = useState({ Benutzer: '', Passwort: '' });
	const [ShowError, setShowError] = useState('');
	const history = useHistory();
	function navigateToSignup() {
		history.push('/signup');
	}
	function navigateToLogin() {
		history.push('/login');
	}
	function navigateToStundenplan() {
		history.push({
			pathname: '/stundenplan',
			state: {
				login: true,
				message: 'Hallo, du bist eingeloggt!',
			},
		});
	}
	const changeBenutzer = (e) => {
		setLogInData({ ...LogInData, Benutzer: e.target.value });
		setShowError('');
	};
	const changePasswort = (e) => {
		setLogInData({ ...LogInData, Passwort: e.target.value });
		setTimeout(() => {
			setShowError('');
		}, 1000);
	};
	const SubmitHandler = async (e) => {
		try {
			e.preventDefault();
		} catch (err) {}

		const response = await axios
			.post('/login', {
				Benutzer: LogInData.Benutzer,
				Passwort: LogInData.Passwort,
			})
			.then((res) => {
				if (res.data) {
					navigateToStundenplan();
				} else {
					console.log('Anmeldedaten inkorrekt');
					setShowError(
						'Anmeldedaten inkorrekt, versuchen Sie es erneut.'
					);
				}
			})
			.catch((err) => {
				console.log('Anmeldung nicht möglich');
			});
	};

	const EnterHandler = (e) => {
		if (e.charCode === 13) {
			SubmitHandler();
		}
	};
	return (
		<div>
			{ShowSomething ? (
				<div className='sign-up'>
					<div className='Login-Wrapper'>
						<div className='logo2b' style={{ fill: '#D45D44' }}>
							<h1>tasc login</h1>
							<IconContext.Provider
								value={{ color: '1c1a2c', size: '2.2rem' }}
							>
								<RiTodoLine />
							</IconContext.Provider>
						</div>

						<div class='Login-Form '>
							<div class='Schule'>
								<div class='form__div'>
									<input
										type='text'
										class='form__input'
										placeholder=' '
										onChange={changeBenutzer}
									/>
									<label for='' class='form__label'>
										Benutzer
									</label>
								</div>
							</div>
							<div class='Benutzer'>
								<select className='which-class'>
									<option value='Java Script'>10A</option>
									<option value='React'>10B</option>
									<option value='Python'>10C</option>
								</select>
							</div>
							<div class='Passwort'>
								<div
									class='form__div'
									onKeyPress={EnterHandler}
								>
									<input
										type='password'
										class='form__input'
										placeholder=' '
										onChange={changePasswort}
									/>
									<label for='' class='form__label'>
										Passwort
									</label>
								</div>
							</div>
							<div className='showErrors'>{ShowError}</div>
						</div>
						<input
							type='submit'
							class='form__button'
							value='Bestätigen'
							onClick={SubmitHandler}
						/>
					</div>
					{/* <div class='l-form'>
					<form action='' class='form' onSubmit={SubmitHandler}>
						<h1 class='form__title'>Login</h1>
						<div class='form-grid'>
							<div class='form__div'>
								<input type='text' class='form__input' placeholder=' ' onChange={changeBenutzer} />
								<label for='' class='form__label'>
									Benutzername
								</label>
							</div>
							<select className='which-class'>
								<option value='Java Script'>10A</option>
								<option value='React'>10B</option>
								<option value='Python'>10C</option>
							</select>
						</div>

						<div class='form__div'>
							<input type='password' class='form__input' placeholder=' ' onChange={changePasswort} />
							<label for='' class='form__label'>
								Passwort
							</label>
						</div>

						<div class='input-redirect' onClick={navigateToSignup}>
							Noch keinen Account?
						</div>
						<input type='submit' class='form__button' value='Bestätigen' />
					</form>
				</div> */}
				</div>
			) : (
				''
			)}
		</div>
	);
}

export default Signup;
