import './App.css';
import React, { useState } from 'react';
import Navbar from './components/navbar/Navbar.js';
import Home from './components/Home.js';
import { Route, Switch, Redirect, BrowserRouter } from 'react-router-dom';
import Footer from './components/footer/Footer';
import Search from './components/Search';
import BackToTop from './components/BackToTop.js';
import impressum from './components/Impressum';
import signup from './components/Signup';
import Login from './components/Login';
import Stundenplan from './components/Stundenplan';
import upload from './components/Upload';
import { ProtectedRoute } from './components/ProtectedRoute';
import { axios } from './components/axios.js';
import { useSelector, useDispatch } from 'react-redux';
import { increment, decrement, logger } from './components/Actions';
function App() {
	//REDUX
	const counter = useSelector((state) => state.loggedReducer);
	const dispatch = useDispatch();

	//REDUX
	const [cursorActive, setCursorActive] = useState(0);
	const [ShowNav, setShowNav] = useState(false);
	const [ShowLogOut, setShowLogOut] = useState(false);
	const hideComponent = (active) => {
		if (active) {
			setShowNav(true);
		} else {
			setShowNav(false);
		}
	};
	const checkStatus = () => {
		axios
			.get('https://tascbackend.herokuapp.com/checkstatus')
			.then((res) => {
				if (res.data) {
					setShowLogOut(true);
					dispatch(logger(true));
				} else {
					setShowLogOut(false);
					dispatch(logger(false));
				}
			});
	};
	const curserActiveHandler = (active) => {
		switch (active) {
			case 0:
				setCursorActive(0);
				break;
			case 1:
				setCursorActive(1);
				break;
			case 2:
				setCursorActive(2);

				break;
			default:
				setCursorActive(0);
				break;
		}
	};
	return (
		<div
			className='App'
			onMouseEnter={() => setCursorActive(2)}
			onMouseLeave={() => setCursorActive(3)}
		>
			<BrowserRouter>
				{/* <Curser active={cursorActive} /> */}

				<Navbar
					eingeloggt={ShowNav}
					logout={ShowLogOut}
					check={checkStatus}
					cursorActivity={curserActiveHandler}
				/>

				<Switch>
					<Route exact path='/impressum' component={impressum} />

					<Route exact path='/signup' component={signup} />
					<Route exact path='/upload' component={upload} />
					<Route
						exact
						path='/login'
						render={(props) => (
							<Login
								{...props}
								hide={hideComponent}
								check={checkStatus}
							/>
						)}
					/>
					<Route
						exact
						path='/'
						render={(props) => (
							<Search
								{...props}
								hide={hideComponent}
								check={checkStatus}
								cursorActivity={curserActiveHandler}
							/>
						)}
					/>

					{/* <ProtectedRoute exact path='/studenplan' component={Stundenplan} /> */}

					<Route
						exact
						path='/stundenplan'
						render={(props) => (
							<Stundenplan
								{...props}
								hide={hideComponent}
								check={checkStatus}
							/>
						)}
						hide={hideComponent}
					/>

					<Redirect to='/' />

					{/* 	<Route component={Search} /> */}
				</Switch>
				<Footer />
			</BrowserRouter>
		</div>
	);
}

export default App;
