function IconHelp(props) {
	const [IconSelected, setIconSelected] = useState(null);
	const klasse = props;
	useEffect(() => {
		switch (klasse) {
			case 'MATHE':
				setIconSelected(<GiAbacus />);
				break;
			default:
				setIconSelected(<GiAbacus />);
				break;
		}
	}, [props, klasse]);
	return IconSelected;
}

export default IconHelp;
