export const increment = () => {
	return {
		type: 'INCREMENT',
	};
};
export const decrement = () => {
	return {
		type: 'DECREMENT',
	};
};
export const logger = (rf = false) => {
	return {
		type: 'SIGN_IN',
		payload: rf,
	};
};

export const termineOpened = (rf = false) => {
	return {
		type: 'TERMINE_OPENED',
		payload: rf,
	};
};
