import React, { useState, useEffect } from 'react';
import * as FaIcons from 'react-icons/fa';
import * as AiIcons from 'react-icons/ai';
import { Link } from 'react-router-dom';
import { SidebarData } from './SidebarData';
import './Navbar.css';
import { IconContext } from 'react-icons';
import BildLogo from '../media/iconStart.svg';
import IconTasc from '../media/iconTasc.png';
import { RiTodoLine } from 'react-icons/ri';
import { useHistory } from 'react-router-dom';
import { axios } from '../axios';
function Navbar(props) {
	const [Eingeloggt, setEingeloggt] = useState(false);
	const history = useHistory();
	function navigateToSignup() {
		history.push('/signup');
		props.cursorActivity(0);
	}
	function navigateToLogin() {
		history.push('/login');
		props.cursorActivity(0);
	}
	const [sidebar, setSidebar] = useState(false);

	const showSidebar = () => setSidebar(!sidebar);
	useEffect(() => {
		if (props.eingeloggt) {
			setEingeloggt(true);
		} else {
			setEingeloggt(false);
		}
	}, [props]);
	const LogOutHandler = () => {
		props.check();
		axios.get('/logout').then(() => {
			history.push('/');
		});
	};
	return (
		<div className={Eingeloggt ? 'hide-everything' : ''}>
			<IconContext.Provider value={{ color: '#4f1964' }}>
				<div className={sidebar ? 'navbar' : 'navbar'}>
					<div className='burger' onClick={showSidebar}>
						<div
							className={
								sidebar ? 'burger-line top-line' : 'burger-line'
							}
						></div>
						<div
							className={
								sidebar
									? 'burger-line middle-line'
									: 'burger-line'
							}
						></div>
						<div
							className={
								sidebar
									? 'burger-line bottom-line'
									: 'burger-line'
							}
						></div>
					</div>
					<div
						className={sidebar ? 'logo' : 'logo'}
						style={{ fill: '#D45D44' }}
					>
						<h1>tasc</h1>
						<IconContext.Provider
							value={{ color: 'D45D44', size: '2rem' }}
						>
							<RiTodoLine />
						</IconContext.Provider>
					</div>
				</div>
				<nav className={sidebar ? 'nav-menu active' : 'nav-menu'}>
					<div
						className={
							sidebar
								? 'nav-content clickedBurger'
								: 'nav-content'
						}
						onClick={showSidebar}
					>
						<div className='login-signup-mobile-wrapper'>
							{!props.logout ? (
								<>
									<div
										className='LogIn'
										onClick={navigateToLogin}
										onMouseEnter={() =>
											props.cursorActivity(1)
										}
										onMouseLeave={() =>
											props.cursorActivity(0)
										}
									>
										LOGIN
									</div>
									<div
										className='SignUp'
										onClick={navigateToSignup}
										onMouseEnter={() =>
											props.cursorActivity(1)
										}
										onMouseLeave={() =>
											props.cursorActivity(0)
										}
									>
										SIGN UP
									</div>
								</>
							) : (
								<div
									className='LogIn'
									onClick={LogOutHandler}
									onMouseEnter={() => props.cursorActivity(1)}
									onMouseLeave={() => props.cursorActivity(0)}
								>
									LOGOUT
								</div>
							)}
						</div>
						<Link to='/'>
							<p className='text1'>Home</p>
						</Link>
						<div className='divider-nav'>'</div>
						<Link to='/impressum'>
							<p className='text1'>Impressum</p>
						</Link>
						<div className='divider-nav'>'</div>
						<Link to='/impressum'>
							<p className='text1'>Über uns</p>
						</Link>
					</div>
				</nav>
			</IconContext.Provider>
			<div className='navbar-pc'>
				<div className='logo2' style={{ fill: '#D45D44' }}>
					<h1 style={{ color: '#D45D44' }}>tasc</h1>
					<IconContext.Provider
						value={{ color: 'D45D44', size: '2rem' }}
					>
						<RiTodoLine />
					</IconContext.Provider>
				</div>
				<div className='login-signup-mobile-wrapper2'>
					{!props.logout ? (
						<>
							<div
								className='LogIn2'
								onClick={navigateToLogin}
								onMouseEnter={() => props.cursorActivity(1)}
								onMouseLeave={() => props.cursorActivity(0)}
							>
								LOGIN
							</div>
							<div
								className='SignUp2'
								onClick={navigateToSignup}
								onMouseEnter={() => props.cursorActivity(1)}
								onMouseLeave={() => props.cursorActivity(0)}
							>
								SIGN UP
							</div>
						</>
					) : (
						<div
							className='LogIn2'
							onClick={LogOutHandler}
							onMouseEnter={() => props.cursorActivity(1)}
							onMouseLeave={() => props.cursorActivity(0)}
						>
							LOGOUT
						</div>
					)}
				</div>
				<div className='Links'>
					<Link
						to='/'
						onMouseEnter={() => props.cursorActivity(1)}
						onMouseLeave={() => props.cursorActivity(0)}
					>
						<p className='text1'>Home</p>
					</Link>

					<Link
						to='/impressum'
						onMouseEnter={() => props.cursorActivity(1)}
						onMouseLeave={() => props.cursorActivity(0)}
					>
						<p className='text1'>Impressum</p>
					</Link>

					<Link
						to='/impressum'
						onMouseEnter={() => props.cursorActivity(1)}
						onMouseLeave={() => props.cursorActivity(0)}
					>
						<p className='text1'>Über uns</p>
					</Link>
				</div>
			</div>
		</div>
	);
}

export default Navbar;
