import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';

function Chart(props) {
	const [chartData, setchartData] = useState({});
	const chart = () => {
		setchartData({
			labels: ['Montag', 'Dienstag', 'Mittwoch', 'Donnerstag', 'Freitag'],
			datasets: [
				{
					label: 'Chart',
					data: [2, 6, 2, 4, 20],
					borderColor: 'black',
					borderWidth: 10,
					pointBorderWidth: 3,
					backgroundColor: ['#d45d44'],
					borderWidth: 4,
					fontColor: 'black',
					pointRadius: 10,
				},
			],
		});
	};
	useEffect(() => {
		chart();
	}, []);
	useEffect(() => {
		//GET DATA

		const { Montag = 0, Dienstag = 0, Mittwoch = 0, Donnerstag = 0, Freitag = 0 } = props.stats;
		console.log(Montag, Dienstag, Mittwoch, Donnerstag, Freitag);
		setchartData({
			labels: ['Montag', 'Dienstag', 'Mittwoch', 'Donnerstag', 'Freitag'],
			datasets: [
				{
					label: 'Chart',
					data: [Montag, Dienstag, Mittwoch, Donnerstag, Freitag],

					backgroundColor: ['#d45d44'],
					borderWidth: 4,
				},
			],
		});
	}, [props.stats]);
	return (
		<div>
			<Line
				data={chartData}
				options={{
					response: true,
					width: '100%',
					title: { text: 'THICCNESS SCALE', display: false },
					scaleFontColor: '#FFF',
					elements: {
						line: {
							borderWidth: 3,
							borderColor: 'white',
							fill: 'bottom',
						},
						point: {
							borderWidth: 10,
							borderColor: 'white',
						},
					},
					legend: {
						display: false,
						labels: {
							fontColor: '#fffffff',
							color: '#fff',
						},
					},
					tooltips: {
						enabled: true,
					},
					scales: {
						yAxes: [
							{
								ticks: {
									autoSkip: true,
									maxTicksLimit: 3,
									beginAtZero: true,
									fontColor: 'white',
								},
								gridLines: {
									display: false,
								},
							},
						],
						xAxes: [
							{
								gridLines: {
									display: false,
								},
								ticks: {
									autoSkip: true,
									maxTicksLimit: 3,
									beginAtZero: true,
									fontColor: 'white',
								},
							},
						],
					},
				}}
			/>
		</div>
	);
}

export default Chart;
